.gallery{
  max-width: 1600px;
  height: 430px;
  background-image: url(../images/mob/gallery-mob.jpg);
  background-repeat: no-repeat;

  
  background-size: 1280px 430px;
 

  @media screen and (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image: url(../images/mob/gallery-mob@2x.jpg);}
      width: 100vw;
      background-size: contain;




      @media screen and(min-width:768px) {
        background-image: url(../images/tablet/gallery-min.jpg);
        width: 100vw;
        
        background-size: contain;
        @media screen and(min-width:768px) and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
        background-image: url(../images/tablet/gallery@2-min.jpg);}

      }

      @media screen and(min-width:1200px){
        background-image: url(../images/desktop/gallery@2-min.jpg);
        filter: drop-shadow(0px 4px 2px rgba(0, 0, 0, 0.25));
        background-size: contain;

        background-size: 100vw;


        @media screen and(min-width:1200px) and (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {


          background-image: url(../images/desktop/gallery@2.jpg);}



      }
}